import Vue from 'vue';

import './assets/styles/main.scss';

import App from './App.vue';
import router from './router';
import store from './store';

import vClickOutside from 'v-click-outside';

Vue.config.productionTip = false;
Vue.use(vClickOutside);

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
